@import "~@smallsforsmalls/colors/colors";
@import "~@smallsforsmalls/breakpoints/breakpoints";

.footer {
  background: $white;
  color: $black;

  padding-top: 4rem;
  padding-bottom: 2rem;

  @include mq($from: tablet) {
    padding-top: 4.5rem;
    padding-bottom: 2.5rem;
  }

  @include mq($from: desktop) {
    padding-top: 5.5rem;
    padding-bottom: 2.5rem;
  }

  p {
    margin-bottom: 0;
    line-height: 2;
  }

  a:visited,
  a:link {
    border: 0;
    padding: 0;
  }
}

.link-group {
  margin-bottom: 2.5rem;
  grid-row: 2;
  grid-column: 1/16;

  @include mq($until: tablet) {
    & + & {
      grid-row: 3;
    }
  }

  @include mq($from: tablet) {
    grid-row: 2;
    margin-bottom: 3rem;
    grid-column: 1/15;
    & + & {
      grid-row: 2;
      grid-column: 16/30;
    }
  }

  @include mq($from: desktop) {
    grid-column: 1/5;
    grid-row: 1/3;
    margin-bottom: 3rem;

    & + & {
      grid-row: 1/3;
      grid-column: 6/10;
    }
  }
}

.title {
  margin-bottom: 1rem;
}

.nav {
  font-size: 1rem;
  line-height: 2rem;
}

.contact {
  grid-row: 4;
  grid-column: 1/10;
  display: flex;
  align-items: baseline;
  justify-content: flex-end;
  flex-direction: column;

  @include mq($from: tablet) {
    grid-row: 3;
    grid-column: 1/27;
  }

  @include mq($from: desktop) {
    display: block;
    grid-row: 1/3;
    grid-column: 11/15;
  }
}

.newsletter {
  grid-row: 1;
  grid-column: 1/16;
  margin-bottom: 8rem;

  @include mq($from: tablet) {
    grid-column: 1/30;
    margin-bottom: 8.5rem;
  }

  @include mq($from: desktop) {
    margin-bottom: 3.3rem;
    grid-column: 17/25;
  }

  p {
    margin: 0;
  }
}

.legal {
  font-size: 0.875rem;
  line-height: 1.25rem;
  grid-row: 6;
  grid-column: 1/17;

  @include mq($from: tablet) {
    grid-column: 1/29;
    grid-row: 5;
  }

  @include mq($from: desktop) {
    margin-top: 1rem;
    grid-column: 1/29;
    grid-row: 3;
  }

  .item {
    display: inline-block;
    margin-right: 4.71rem;
    @include mq($from: tablet) {
      margin-right: 2.5rem;
    }
  }
}

.social-nav {
  grid-row: 5;
  grid-column: 1/16;
  margin-top: 2.8rem;
  margin-bottom: 2.6rem;

  @include mq($from: tablet) {
    grid-row: 4;
    grid-column: 1/27;
    margin-top: 3.3rem;
    margin-bottom: 3rem;
  }

  @include mq($from: desktop) {
    grid-column: 17/24;
    grid-row: 2;
    margin-top: 4rem;
    margin-bottom: 0;
  }

  .image {
    width: 1.5rem;
    height: 1.5rem;
    display: block;
  }

  .item {
    display: inline-block;
    margin-right: 2.2rem;
    height: 64px;
    width: 64px;
    @include mq($from: tablet) {
      margin-right: 0;
    }

    &:first-child {
      margin-left: -19px;
    }

    a {
      border-radius: 50%;
      display: grid;
      justify-content: center;
      align-content: center;
      height: 100%;

      &:hover {
        background-color: $lightblue;
      }
    }
  }
}

.copyright {
  grid-row: 4;
  grid-column: 14/17;
  justify-self: flex-end;
  display: flex;
  align-self: flex-end;

  @include mq($from: tablet) {
    grid-row: 3;
    grid-column: 27/30;
  }

  @include mq($from: desktop) {
    grid-row: 1/3;
    padding-bottom: 1.25rem;
  }

  img {
    width: 95px;
    height: 95px;
  }
}

.back-to-top {
  justify-self: flex-end;
  display: flex;
  grid-row: 5;
  grid-column: 16;

  @include mq($from: tablet) {
    grid-row: 4;
    grid-column: 29;
  }

  @include mq($from: desktop) {
    margin-top: 4rem;
    grid-row: 2;
    grid-column: 24;
  }
}
