@import "~@smallsforsmalls/colors/colors";
@import "~@smallsforsmalls/breakpoints/breakpoints";

.header {
  padding: 20px 25px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid $black;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;

  @include mq($until: tablet) {
    padding: 20px 25px;
  }

  &.-light {
    border-color: $yellow;

    .logo {
      fill: $yellow;
    }

    .link {
      color: $yellow;
    }
  }

  &.-dark {
    .logo {
      fill: $black;
    }

    .link {
      color: $black;
    }
  }
}

.link {
  &:link,
  &:visited {
    text-decoration: none;
    border: 0;
  }
}

.heading {
  margin: 0;
  text-align: center;
  line-height: 1rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.logo {
  width: 115px;
  height: 16px;
  display: block;
  margin: auto;
}

.nav {
  flex: 1 1 auto;

  > .list {
    list-style: none;
    display: flex;
    padding: 0;
  }

  &.-left {
    order: 1;
    > .list {
      > .item {
        margin-right: 45px;
      }
    }
  }
  &.-right {
    order: 3;
    @include mq($until: tablet) {
      display: none;
    }
    > .list {
      justify-content: flex-end;
      > .item {
        margin-left: 45px;
      }
    }
  }
}

.nav-toggler {
  padding: 0;
  cursor: pointer;
  border: 0;
  background: transparent;
  width: 24px;
  height: 24px;

  &.-light {
    .image {
      fill: $yellow;
    }
  }

  &.-dark {
    .image {
      fill: $black;
    }
  }

  > .image {
    display: block;
    width: 24px;
    height: 24px;
  }
}

.mobile-nav {
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  border-right: 1px solid #000;
  width: 384px;
  z-index: 2;
  display: flex;
  flex-direction: column;
  background: $warmgrey no-repeat bottom 112px right 45px
    url("../Images/parachute-cat.svg");
  color: $black;

  .nav-toggler {
    margin: 25px 0 49px 29px;
    width: 14px;
    height: 14px;
  }

  .list {
    flex: 1 1 auto;
    padding-left: 25px;
  }

  .image {
    width: 14px;
    height: 14px;
    stroke: $black;
  }

  .item {
    font-size: 1.75rem;
    line-height: 1;
    margin-top: 1rem;

    &:first-child {
      margin-top: 0;
    }

    &.-child {
      font-size: 1.125rem;
      line-height: 1.75rem;
      margin-top: 1.5rem;
      margin-left: 1.5rem;
    }
  }
}

.shade {
  display: block;
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.get-started-cta {
  display: block;
  width: 100%;
  line-height: 1.5rem;
}

.hamburguer {
  flex: 1 1 auto;
  @include mq($until: tablet) {
    flex: 0 0 auto;
  }
}
