@import "~@smallsforsmalls/colors/colors";
@import "~@smallsforsmalls/breakpoints/breakpoints";

.carousel {
  position: relative;

  .arrows {
    background-color: $yellow;
    border: 1px solid;
    border-color: $black;
    width: 48px;
    height: 48px;
    border-radius: 100%;
    cursor: pointer;
    transition: all 500ms ease;
    z-index: 2;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 20px;

    &:hover {
      background-color: $warmgrey;
    }

    &:disabled {
      cursor: default;
      background-color: $warmgrey;
      border-color: $warmgrey;

      svg {
        opacity: 0.5;
      }
    }

    svg {
      margin: 2px 0 0 -3px;
    }

    &:last-child {
      right: 20px;
      left: auto;

      svg {
        margin: 2px -3px 0 0;
      }
    }
  }

  &.-indicators {
    margin-bottom: 40px;
  }
}

.slides {
  > div {
    display: grid;
    grid-template-columns: repeat(24, 1fr);

    @include mq($from: laptop) {
      justify-content: center;
      grid-template-columns: repeat(20, 1fr);
    }
  }

  &.-col20 {
    > div {
      @include mq($from: laptop) {
        grid-template-columns: repeat(20, 1fr);
      }
    }
  }

  &.-col21 {
    > div {
      @include mq($from: laptop) {
        grid-template-columns: repeat(21, 1fr);
      }
    }
  }

  &.-col28 {
    > div {
      @include mq($from: laptop) {
        grid-template-columns: repeat(28, 1fr);
      }
    }
  }

  &.-on {
    overflow: hidden;

    > div {
      display: flex;

      div {
        position: relative;
      }
    }

    &.-full {
      > div {
        div {
          min-width: 100%;
        }
      }
    }

    &.-peaking {
      > div {
        div {
          min-width: 55%;

          @include mq($from: tablet) {
            min-width: 45%;
          }

          @include mq($from: laptop) {
            min-width: 35%;
          }
        }
      }
    }
  }
}
