@import "~@smallsforsmalls/colors/colors";

.button {
  font-size: inherit;
  line-height: inherit;
  font-family: inherit;

  padding: 20px 35px;

  cursor: pointer;
  transition: color 0.2s ease 0s, background-color 0.2s ease 0s;

  border: 0;
  min-width: 180px;

  &.-primary {
    color: $white;
    background: $black;

    &:disabled {
      cursor: not-allowed;

      color: $black8;
      background-color: $darkgrey;
    }

    &:not(:disabled):hover {
      color: $black;
      background-color: $yellow;
    }

    &.-light {
      color: $black;
      background: $yellow;
      &:not(:disabled):hover {
        color: $yellow;
        background-color: $black;
      }
    }
  }

  &.-secondary {
    color: $black;
    border: 1px solid $black;
    background-color: transparent;

    &:not(:disabled):hover {
      background-color: $greige;
    }

    &.-light {
      color: $yellow;
      border: 1px solid $yellow;
      &:not(:disabled):hover {
        color: $black;
        background-color: $yellow;
      }
    }
  }
}
