@import "~@smallsforsmalls/breakpoints/breakpoints";

.item {
  display: grid;
  grid-template-columns: repeat(1, 1fr);

  .label {
    text-transform: uppercase;
  }

  .picture {
    picture,
    img {
      display: block;
      margin: auto;
    }
  }

  .description p {
    font-size: 1rem;
    line-height: 1.5rem;
    margin-bottom: 1.375rem;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &.-layout-image {
    .label {
      display: none;
      @include mq($from: laptop) {
        display: block;
        text-align: center;
        padding-bottom: 1.5rem;
      }
    }

    .title {
      font-size: 0.75rem;
      line-height: 0.75rem;
      font-family: "Adieu", sans-serif;
      text-transform: uppercase;
      margin: 0;
      padding-bottom: 0.8125rem;
      padding-top: 1rem;

      @include mq($from: tablet) {
        padding-bottom: 1rem;
        padding-top: 1.6875rem;
      }

      @include mq($from: laptop) {
        font-size: 1.5rem;
        line-height: 2rem;
        font-family: "Monument", sans-serif;
        text-transform: none;
        padding-bottom: 0.625rem;
        padding-top: 1.3125rem;
      }
    }

    .description p {
      font-size: 1.25rem;
      line-height: 1.75rem;

      @include mq($from: tablet) {
        font-size: 1.5rem;
        line-height: 2rem;
      }

      @include mq($from: laptop) {
        font-size: 1rem;
        line-height: 1.5rem;
      }
    }

    &.-with-carousel {
      display: block;
      @include mq($from: tablet) {
        display: grid;
        align-content: flex-start;
      }

      .label {
        display: block;
        text-align: center;
        padding-bottom: 1rem;
        @include mq($from: tablet) {
          padding-bottom: 1.5rem;
        }
      }

      .title {
        font-family: Monument, sans-serif;
        font-size: 1.25rem;
        line-height: 1.75rem;
        text-transform: none;
        padding-bottom: 0.625rem;
        @include mq($from: tablet) {
          font-size: 1.5rem;
          line-height: 2rem;
          padding-top: 1.4375rem;
          padding-bottom: 0.625rem;
        }
      }

      .description p {
        font-size: 1rem;
        line-height: 1.4rem;
      }
    }
  }

  &.-layout-product {
    margin: 0 auto;

    .picture {
      grid-row: 1;
      grid-column: 1/-1;
    }

    .label {
      grid-row: 2;
      grid-column: 1/-1;
      padding-bottom: 0.9rem;
      padding-top: 1rem;
      margin: 0;
      @include mq($from: tablet) {
        padding-top: 1.5rem;
        padding-bottom: 0.75rem;
      }
    }

    .title {
      grid-row: 3;
      grid-column: 1/-1;
      font-size: 1.25rem;
      line-height: 1.75rem;
      margin: 0;
      padding-bottom: 0.5rem;

      @include mq($from: tablet) {
        font-size: 1.5rem;
        line-height: 2rem;
        padding-bottom: 0.625rem;
      }
    }

    .description {
      grid-row: 4;
      grid-column: 1/-1;
    }

    &.-with-carousel {
      .label {
        padding-top: 1rem;
        padding-bottom: 0.875rem;
        @include mq($from: tablet) {
          padding-top: 1.5rem;
        }
      }
      .title {
        padding-bottom: 0.5rem;
      }
    }
  }

  &.-layout-icon {
    margin-bottom: 4.2rem;

    @include mq($from: tablet) {
      margin-bottom: 4.7rem;
    }

    @include mq($from: laptop) {
      margin-bottom: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }

    .title {
      font-size: 0.75rem;
      line-height: 0.75rem;
      font-family: "Adieu", sans-serif;
      text-align: center;
      letter-spacing: 1px;
      padding-top: 2rem;
      padding-bottom: 0.625rem;
      margin: 0;
      text-transform: uppercase;

      @include mq($from: tablet) {
        padding-top: 2.125rem;
        padding-bottom: 0.75rem;
      }
    }

    .description {
      text-align: center;
      line-height: 1.5rem;

      @include mq($from: tablet) {
        max-width: 480px;
        margin: auto;
      }
      p {
        margin: 0;
      }
    }

    .picture img {
      width: 120px;
      height: 120px;

      @include mq($from: tablet) {
        width: 135px;
        height: 135px;
      }
    }
  }
}
