@import "~@smallsforsmalls/colors/colors";
@import "~@smallsforsmalls/breakpoints/breakpoints";

.three-up {
  padding: 4rem 0;
  background-color: $white;
  @include mq($from: tablet) {
    padding: 4.5rem 0;
  }
  @include mq($from: laptop) {
    padding: 5.5rem 0;
  }
}

.-greige {
  background-color: $greige;
}

.header {
  grid-column: 1/-1;
  grid-row: 1;
  text-align: center;
  margin-bottom: 2.5rem;

  @include mq($from: tablet) {
    max-width: 576px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 3rem;
  }

  @include mq($from: laptop) {
    max-width: 540px;
  }
}

.main-title {
  margin: 0;
  margin-bottom: 0.625rem;
  font-size: 2rem;
  line-height: 2.4rem;

  @include mq($from: tablet) {
    font-size: 2.25rem;
    line-height: 2.5rem;
  }

  @include mq($from: laptop) {
    margin-bottom: 0.8125rem;
    font-size: 2.5rem;
  }
}

.description {
  margin: 0;
}

.items {
  grid-column: 1/-1;
  grid-row: 2;
}

.cta {
  grid-column: 1/-1;
  grid-row: 3;
  text-align: center;
  padding-top: 4rem;
  margin-bottom: 0;
}

.-with-carousel {
  .cta {
    padding-top: 0;
    @include mq($from: laptop) {
      padding-top: 4rem;
    }
  }
}

// Overrides

.-layout-product {
  &.-with-carousel {
    padding-bottom: 2.125rem;

    .items {
      margin: 0 -0.625rem;
      @include mq($from: tablet) {
        margin: 0;
        max-width: 404px;
      }
      @include mq($from: laptop) {
        max-width: none;
      }
    }
  }

  .items {
    @include mq($from: tablet) {
      margin: 0;
      position: relative;
      transform: translateX(-50%);
      left: 50%;
      max-width: 384px;
    }

    .item {
      padding-bottom: 4.5rem;
    }
  }
}

.-layout-image {
  .main-title {
    margin: 0;
    @include mq($from: laptop) {
      padding-bottom: 0.625rem;
    }
  }

  .description {
    display: none;
    @include mq($from: laptop) {
      display: block;
    }
  }

  &.-with-carousel {
    .main-title {
      padding-bottom: 0.9375rem;
      @include mq($from: tablet) {
        padding-bottom: 0.625rem;
      }
      @include mq($from: laptop) {
        padding-bottom: 0.9375rem;
      }
    }
    .description {
      display: block;
    }

    .items {
      margin: 0 -0.625rem;
      @include mq($from: laptop) {
        margin: 0;
      }
    }

    .item {
      padding-bottom: 0;
    }
  }

  .item {
    padding-bottom: 4rem;

    @include mq($from: tablet) {
      padding-bottom: 4.5rem;
    }

    @include mq($from: laptop) {
      padding-bottom: 2.5rem;
    }

    &:last-child {
      padding-bottom: 1.5rem;
    }
  }
}

.-layout-icon {
  .header {
    display: none;
  }
}

.-layout-image,
.-layout-product,
.-layout-icon {
  @include mq($from: laptop) {
    .items {
      max-width: none;
      display: grid;
      grid-template-columns: repeat(28, 1fr);
    }

    .item {
      grid-column: 1/9;
    }

    .item:nth-child(2) {
      grid-column: 11/19;
    }

    .item:nth-child(3) {
      grid-column: 21/29;
    }
  }

  &.-with-carousel {
    .items {
      display: block;
    }

    .item {
      padding: 0 0.625rem 2.25rem;
      @include mq($from: tablet) {
        padding: 0 0.625rem 2.6875rem;
      }
      @include mq($from: laptop) {
        padding: 0;
      }
    }
  }
}
