@import "~@smallsforsmalls/colors/colors";
@import "~@smallsforsmalls/breakpoints/breakpoints";

.centeralignedtext-content {
  text-align: center;
  padding: 2.5rem 0;

  @include mq($from: tablet) {
    padding: 0;
  }

  > picture,
  > button,
  > h3 {
    grid-column-start: 2;
    grid-column-end: 18;
    justify-self: center;

    @include mq($from: tablet) {
      grid-column-start: 4;
      grid-column-end: 30;
    }

    @include mq($from: laptop) {
      grid-column-start: 9;
      grid-column-end: 25;
    }
  }

  h3 {
    margin: 0 0 40px;
    @include mq($from: laptop) {
      margin: 0 0 66px;
    }
  }

  picture {
    margin: 0 0 30px;
  }

  .cta {
    margin-top: 0;

    @include mq($from: laptop) {
      margin-top: 60px;
    }
  }

  &.-off {
    > div {
      display: grid;
    }
    .cta {
      display: inline;
    }
  }

  > div {
    grid-column-end: 19;
    grid-column-start: 1;
    grid-template-columns: repeat(16, 1fr);
    justify-content: center;
    justify-self: center;
    width: 100%;

    @include mq($from: tablet) {
      grid-column-start: 8;
      grid-column-end: 26;
      grid-template-columns: repeat(24, 1fr);
    }

    @include mq($from: laptop) {
      grid-column-start: 7;
      grid-column-end: 27;
      grid-template-columns: repeat(20, 1fr);
    }
  }

  > picture {
    max-width: 90px;
    max-height: 90px;
  }
}

.slides {
  grid-column: span 8;
  margin-bottom: 40px;

  @include mq($from: tablet) {
    margin-bottom: 48px;
    grid-column: span 12;
  }

  @include mq($from: laptop) {
    grid-column: span 4;
    margin-bottom: 0;
  }

  &.-one {
    grid-column: span 16;
    @include mq($from: tablet) {
      grid-column: span 26;
    }
    &:first-child {
      @include mq($from: laptop) {
        grid-column-start: 9;
        grid-column-end: 13;
      }
    }
  }

  &.-two {
    @include mq($from: laptop) {
      grid-column: span 4;
    }
    &:first-child {
      @include mq($from: laptop) {
        grid-column-start: 7;
        grid-column-end: 11;
      }
    }
  }

  &.-three {
    &:last-child {
      grid-column: span 16;
      @include mq($from: tablet) {
        grid-column: span 24;
      }
      @include mq($from: laptop) {
        grid-column: span 4;
      }
    }
    &:first-child {
      @include mq($from: laptop) {
        grid-column-start: 5;
        grid-column-end: 9;
      }
    }
  }

  &.-four {
    @include mq($from: tablet) {
      grid-column: span 12;
    }
    @include mq($from: laptop) {
      grid-column: span 4;
    }
    &:first-child {
      @include mq($from: laptop) {
        grid-column-start: 3;
        grid-column-end: 7;
      }
    }
  }

  &.-five {
    @include mq($from: tablet) {
      grid-column: span 8;
    }
    @include mq($from: laptop) {
      grid-column: span 4;
    }
    &:nth-child(4),
    &:nth-child(5) {
      @include mq($from: tablet) {
        grid-column: span 12;
      }
      @include mq($from: laptop) {
        grid-column: span 4;
      }
    }
  }

  img {
    max-width: 48px;
    max-height: 48px;
  }

  p {
    text-align: center;
    margin: 14px 0 0;
  }
}
