@import "~@smallsforsmalls/colors/colors";
@import "~@smallsforsmalls/breakpoints/breakpoints";

.background-image {
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  z-index: 0;
  overflow: hidden;

  &.-peaking {
    height: 450px;

    @include mq($from: tablet) {
      height: 100%;
    }
  }
}

.atf-content {
  height: 100%;
  display: grid;

  h1 {
    max-width: 290px;
    @include mq($from: desktop_xl) {
      max-width: 380px;
    }
  }

  > div {
    @include mq($from: tablet) {
      margin-top: -30px;
    }
  }

  &.-peaking {
    h1 {
      margin: 30px 0 9px;
      @include mq($from: tablet) {
        margin: 0 0 18px;
      }
    }

    p {
      @include mq($from: tablet) {
        margin: 0 0 35px;
      }
    }

    > div {
      padding: 0 0 40px;
      @include mq($from: tablet) {
        padding: 95px 0 0;
      }
      @include mq($from: laptop) {
        padding: 0;
      }
    }

    &.-bottom,
    &.-top {
      grid-template-rows: 425px auto;

      @include mq($from: laptop) {
        > div {
          grid-row-start: auto;
        }
      }

      > div {
        grid-row-start: 2;
        align-self: end;
        @include mq($from: tablet) {
          align-self: auto;
        }
        @include mq($from: laptop) {
          align-self: center;
        }
      }
    }

    &.-bottom {
      grid-template-rows: 425px auto;

      @include mq($from: tablet) {
        grid-template-rows: auto auto;
      }

      @include mq($from: laptop) {
        grid-template-rows: 0 auto;
      }

      > div {
        align-self: end;

        @include mq($from: laptop) {
          align-self: center;
        }
      }
    }

    &.-top {
      @include mq($from: tablet) {
        > div {
          grid-row-start: auto;
        }
      }
      @include mq($from: laptop) {
        grid-template-rows: auto 0;
      }
    }

    &.-center {
      > div {
        text-align: left;
      }
    }
  }

  &.-bottom,
  &.-top {
    > div {
      grid-column-start: 1;
      grid-column-end: 16;

      @include mq($from: tablet) {
        grid-column-start: 1;
        grid-column-end: 20;
      }

      @include mq($from: laptop) {
        grid-column-start: 1;
        grid-column-end: 12;
      }
    }
  }

  &.-left {
    > div {
      @include mq($from: laptop) {
        align-self: center;
        grid-column-start: 1;
        grid-column-end: 11;
      }
    }
  }

  &.-right {
    > div {
      @include mq($from: laptop) {
        align-self: center;
        grid-column-start: 19;
        grid-column-end: 33;
      }
    }
  }

  &:not(.-peaking) {
    &.-top {
      > div {
        padding-top: 82px;
        @include mq($from: laptop) {
          padding-top: 0;
        }
      }
    }

    &.-bottom {
      grid-template-rows: auto 333px;
      @include mq($from: tablet) {
        grid-template-rows: auto 378px;
        > div {
          padding-top: 52px;
        }
      }
      @include mq($from: laptop) {
        grid-template-rows: 1fr;
        > div {
          grid-row-start: 1;
          padding-top: 0;
        }
      }
      > div {
        grid-row-start: 2;
        @include mq($from: laptop) {
          grid-row-start: auto;
        }
      }
    }

    &.-center {
      grid-template-rows: 1fr;

      h1 {
        margin: 5px auto;
        @include mq($from: mobile) {
          margin: 0 auto 20px;
        }
      }

      p {
        @include mq($from: mobile) {
          margin-bottom: 34px;
        }
      }

      > div {
        align-self: center;
        grid-column-start: 1;
        grid-column-end: 17;
        grid-row-start: auto;
        padding-top: 0;

        @include mq($from: tablet) {
          grid-column-start: 3;
          grid-column-end: 25;
        }

        @include mq($from: laptop) {
          grid-column-start: 9;
          grid-column-end: 21;
        }

        @include mq($from: desktop) {
          grid-column-start: 10;
          grid-column-end: 20;
        }
      }
    }
  }
}
