@import "~@smallsforsmalls/colors/colors";
@import "~@smallsforsmalls/breakpoints/breakpoints";

.module-wrapper {
  background-color: $greige;
  padding: 1.5rem 0;
  width: 100%;
  height: auto;
  position: relative;

  @include mq($from: tablet) {
    padding: 4.5rem 0;
  }

  @include mq($from: laptop) {
    padding: 5.5rem 0;
  }

  &.-border {
    border-bottom: 1px solid $black;
  }

  &.-light {
    color: $yellow;
  }

  &.-dark {
    color: $black;
  }

  &.-fullscreen {
    height: 100vh;
  }

  &.-peaking {
    height: auto;

    @include mq($from: tablet) {
      height: 80vh;
    }

    @include mq($from: laptop) {
      height: 77vh;
    }
  }

  &.-warmgrey {
    background-color: $warmgrey;
  }

  &.-greige {
    background-color: $greige;
  }

  &.-white {
    background-color: $white;
  }

  &.-yellow {
    background-color: $yellow;
  }

  &.-lightblue {
    background-color: $lightblue;
  }

  &.-beef {
    background-color: $beef;
  }

  &.-duck {
    background-color: $duck;
  }

  &.-turkey {
    background-color: $turkey;
  }

  &.-fish {
    background-color: $fish;
  }

  &.-sage {
    background-color: $sage;
  }

  &.-beige {
    background-color: $beige;
  }
}
