@import "~@smallsforsmalls/colors/colors";

.indicators {
  display: flex;
  justify-content: center;

  .step {
    display: inline-block;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    border: 1px solid $black;
    margin-left: 8px;
    padding: 0;

    &:first-child {
      margin: 0;
    }

    &.-complete {
      background: $yellow;
    }

    &.-clickable {
      cursor: pointer;

      &:hover {
        background: $yellow;
      }
    }
  }
}
