@import "~@smallsforsmalls/colors/colors";
@import "~@smallsforsmalls/breakpoints/breakpoints";

.grid {
  display: grid;
  grid-template-columns: repeat(18, 1fr);
  max-width: 1440px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
  height: 100%;

  @include mq($from: tablet) {
    grid-template-columns: repeat(32, 1fr);
  }

  &.-full {
    > div {
      grid-column-start: 1;
      grid-column-end: 19;

      display: grid;
      grid-template-columns: repeat(18, 1fr);

      @include mq($from: tablet) {
        grid-column-start: 1;
        grid-column-end: 33;

        display: grid;
        grid-template-columns: repeat(32, 1fr);
      }
    }
  }

  &:not(.-full) {
    > div {
      grid-column-start: 2;
      grid-column-end: 18;

      display: grid;
      grid-template-columns: repeat(16, 1fr);

      @include mq($from: tablet) {
        grid-column-start: 4;
        grid-column-end: 30;

        display: grid;
        grid-template-columns: repeat(26, 1fr);
      }

      @include mq($from: laptop) {
        grid-column-start: 3;
        grid-column-end: 31;

        grid-template-columns: repeat(28, 1fr);
      }
    }
  }
}
